/* App.css */

.rotating-image {
    animation: rotate 20s linear infinite;
    max-width: 100%;
   
  }
  
  @keyframes rotate {
    from {
      transform: rotate(0deg); /* Start from 0 degrees rotation */
    }
    to {
      transform: rotate(360deg); /* Rotate to 360 degrees */
    }
  }
  